.cartItem {
	display: grid;
	grid-template-columns: 110px 1fr;
	grid-template-rows: 55px 55px;
}

.cartItem + .cartItem {
	border-top: 1px solid var(--border);
	padding-top: 1rem;
	margin-top: 1rem;
}

.cartItem .img {
	grid-column: 1;
	grid-row: 1 / 3;
	width: 100%;
	height: 100%;
}

.img img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.header {
	padding: 0 0.5rem 0 1rem;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	width: 100%;
}

.titleAndPrice {
	text-align: left;
	margin-right: 2rem;
}

.title {
	font-weight: bold;
	max-width: 7rem;
	line-height: 100%;
}

.unitPrice {
	font-size: 0.8rem;
	margin-top: 0.4rem;
}

.buttonsAndQuantity {
	align-self: end;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 0.5rem 0 1rem;
}

.value {
	font-weight: bold;
}

.buttons button {
	width: 30px;
	height: 30px;
	background: var(--text-1);
	color: var(--bg);
	border-radius: 0.2rem;
}

.buttons button:nth-of-type(1) {
	margin-right: 0.2rem;
}

@media (max-width: 450px) {
	.cartItem {
		display: grid;
		grid-template-columns: 80px 1fr;
		grid-template-rows: 60px 60px;
	}
}
